.cta__block {
  margin: 40px 0px; }

.cta__action {
  font-family: 'futura-pt', 'sans-serif';
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  background-color: #fff;
  border-color: #0d0d0d;
  border-style: solid;
  border-width: 2px;
  border-radius: 3px;
  padding: 13px 26px;
  transition: background-color 0.1s ease-out, color 0.1s ease-out; }

.cta__action:hover {
  color: #fff;
  background-color: #0d0d0d;
  border-color: #0d0d0d; }
