@import url("https://fonts.googleapis.com/css?family=Raleway:200,400|Work+Sans:200,400");
@import url("https://use.typekit.net/fha1rpn.css");
body {
  color: #444;
  font-size: 16px;
  font-family: "ff-meta-serif-web-pro", "serif";
  letter-spacing: 0.16px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: subpixel-antialiased;
  background-color: #fefefe;
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center; }

h1, h2, h3, h4, h5 {
  font-family: "brandon-grotesque", "sans-serif";
  font-weight: 400;
  padding: 0px;
  margin: 0px; }

h2 {
  font-size: 35px;
  letter-spacing: -0.35px;
  line-height: 35px;
  margin-block-end: 29.05px; }

h3 {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2.4px;
  line-height: 25.6px;
  margin-top: 15px;
  margin-block-end: 29.05px;
  margin-block-start: 29.05px; }

p {
  line-height: 27.2px;
  font-weight: 500; }

a {
  color: inherit;
  text-decoration: none;
  padding-bottom: .05em;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgba(28, 28, 28, 0.3);
  transition: border-color .15s ease-out, color .15s ease-out; }

a:hover {
  color: rgba(28, 28, 28, 0.6);
  border-color: rgba(28, 28, 28, 0.15); }

.parallax {
  position: relative;
  height: 500px;
  /* fallback for older browsers */
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 300px;
  perspective: 300px;
  -webkit-perspective-origin-x: 100%;
  perspective-origin-x: 100%; }

.parallax__group {
  position: relative;
  height: 80vh;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d; }

.parallax__layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform-origin-x: 100%;
  transform-origin-x: 100%; }

.parallax__layer--fore {
  -webkit-transform: translateZ(90px) scale(0.7);
  transform: translateZ(90px) scale(0.7);
  z-index: 1; }

.parallax__layer--base {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  z-index: 4; }

.parallax__layer--back {
  -webkit-transform: translateZ(-300px) scale(2);
  transform: translateZ(-300px) scale(2);
  z-index: 3; }

.parallax__layer--deep {
  -webkit-transform: translateZ(-600px) scale(3);
  transform: translateZ(-600px) scale(3);
  z-index: 2; }

.content {
  display: grid;
  justify-content: center; }
  .content .content__callout {
    text-align: center; }

.padding__high {
  padding-top: 100px;
  padding-bottom: 100px; }

.padding__medium {
  padding-top: 60px;
  padding-bottom: 60px; }

.padding__low {
  padding-top: 30px;
  padding-bottom: 30px; }

.width__wide {
  width: 90vw; }

.width__medium {
  width: 90vw; }
  @media all and (min-width: 960px) {
    .width__medium {
      width: 80vw; } }

.width__narrow {
  width: 90vw; }
  @media all and (min-width: 960px) {
    .width__narrow {
      width: 60vw; } }

.header {
  display: grid;
  grid-template-columns: 80px auto;
  grid-template-areas: "navigation logo";
  align-items: center;
  justify-items: center;
  height: 60px;
  width: 100vw;
  background-color: #fafafa; }
  @media all and (min-width: 960px) {
    .header {
      height: 80px;
      grid-template-columns: 300px auto;
      grid-template-areas: "logo navigation"; } }

.logo {
  grid-area: logo;
  justify-self: start;
  padding-left: 16px; }
  @media all and (min-width: 960px) {
    .logo {
      padding-left: 32px; } }

.logo h1 {
  font-family: "Work Sans", "sans-serif";
  font-size: 17px;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 4.25px; }
